import "./styles.css";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "./createactions";
export default function App() {
  const state = useSelector((state) => state.reducer.count);
  const dispatch = useDispatch();
  return (
    <div className="App">
      <button onClick={(e) => dispatch(actions.increament({ name: "Angkit" }))}>
        increament
      </button>
      <div>
        <button onClick={() => dispatch(actions.reset())}>reset</button>
      </div>
      <button onClick={() => dispatch(actions.decreament())}>decreament</button>
      <hr />
      <div>{state}</div>
    </div>
  );
}
