import { handleActions } from "redux-actions";
import * as constants from "./constants";

const initialState = {
  count: 0
};

const increament = (state, action) => {
  return {
    ...state,
    count: state.count === 12 ? state.count : state.count + 1
  };
};

const decreament = (state, action) => {
  return { ...state, count: state.count === 0 ? state.count : state.count - 1 };
};

const reset = (state, action) => {
  return { ...state, count: 0 };
};

export default handleActions(
  {
    [constants.INCREAMENT]: increament,
    [constants.DECREAMENT]: decreament,
    [constants.RESET]: reset
  },
  initialState
);
